import type { Recruit } from 'src/models/Recruit'

export const formatUnivEnteredYearLimit = (recruitInfoTest: Recruit): string => {
  const least = recruitInfoTest.minGrade
  const max = recruitInfoTest.maxGrade
  if (least && max) {
    return `${least}학번 ~ ${max}학번`
  } else if (least === null && max) {
    return `${max}학번 이하`
  } else if (least && max === null) {
    return `${least}학번 이상`
  } else {
    return '무관'
  }
}
